import { registerFilter } from 'nornj';

registerFilter({
  clickBtn: function(fn, type) {
    let { pageIndex, pageCount } = this.data[0];

    switch(type){
      case 'first':
        return () => {
          if(pageIndex != 1) {
            fn(1);
          }
        };
      case 'prev':
        return () => {
          if(pageIndex > 1) {
            fn(pageIndex - 1);
          }
        };
      case 'next':
        return () => {
          if(pageIndex < pageCount) {
            fn(pageIndex + 1);
          }
        };
      case 'last':
        return () => {
          if(pageIndex != pageCount) {
            fn(pageCount);
          }
        };
      case 'index':
        return () => {
          if(this.index != pageIndex) {
            fn(this.index);
          }
        };
      default:
        return () => fn();
    }
  },
  isCurrentPage: function(no) {
    return parseInt(no, 10) == this.data[0].pageIndex ? '-c' : '';
  },
  showPartPage: function(no, type) {
    let { pageCount } = this.data[0];
    switch(type){
      case '1':  //<=4:++2+...+
        return no <= 4;
      case '2':  //>4,<=(n)-3:+...+2+ +2+...+
        return no > 4 && no <= pageCount - 3;
      case '3':  //>(n)- 3:+...+2++
        return no > pageCount - 3;
    }
  }
});


